@use '../colors.scss';

#customer-experience
{
    .mainTitle
    {
        position: relative;
        align-self: center;
        margin: 50px 0;

        &::after
        {
            content: '';
            position: absolute;
            bottom: -.25ch;
            left: 0;
            height: .2ch;
            width: 100%;
            background-color: black;
            border-radius: 1em;
        }
    }

    .big, .small { margin: 5vh 10vw; }

    .big
    {
        justify-content: center;
        gap: 12.5vw;
        align-items: center;
        
        img
        {
            height: auto;
            width: 40%;
            border-radius: 1em;
        }

        .content
        {
            gap: 3em;
            max-width: 500px;

            .descsmall
            {
                gap: 1em;
            }
        }
    }

    .small
    {
        .title
        {
            position: relative;

            &::after
            {
                content: '';
                position: absolute;
                bottom: -.5ch;
                left: 0;
                height: .2ch;
                width: 100%;
                background-color: black;
                border-radius: 1em;
            }
        }

        .content
        {
            margin: 2em 0;
            gap: 1em;
        }
    }
}