@use './colors.scss';

#pricing
{
    align-items: center;
    margin: 7vh 0;

    gap: 5vh;

    .head { text-align: center; }

    .prices
    {
        gap: 7vw;

        .tier
        {
            width: 35vw;
            background-color: hsl(0, 0%, 97%);
            border: 2.5px solid colors.$accent;
            border-radius: 1em;
            padding: 2vh 0;
            gap: 2.5vh;

            .price
            {
                text-align: center;
                padding: 0 1cqw;
                gap: 1ch;

                .desc { font-weight: bold; }

                .cost
                {
                    font-size: 4em;
                    font-weight: bold;
                }

                .descsmall { font-size: 1.5em; }
            }

            .features
            {
                padding: 0 2.5cqw;
                gap: 2ch;

                .feature
                {
                    align-items: center;
                    gap: 2em;

                    svg
                    {
                        scale: 1.5;
                        color: colors.$accent;
                    }
                }
            }
        }
    }
}