@use './colors.scss';

@keyframes load
{
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

#tool
{
    $dark: rgba(0, 0, 0, 0.475);
    gap: 5vh;
    margin: 5vh 10vw;
    min-height: 80%;
    justify-content: center;

    .loader
    {
        position: absolute;
        color: colors.$accent;
        aspect-ratio: 1;
        box-sizing: border-box;
        border-radius: 50%;
        border: 1em solid currentColor;
        border-color: currentColor transparent currentColor transparent;
        animation: load 750ms ease-in-out infinite;

        &.show { display: flex; }
        &.hide { display: none; } 
    }

    ul, ol
    {
        margin: 0;
        list-style: none;
            
        li
        {
            position: relative;

            &::before
            {
                position: absolute;
                content: '';
                height: .5em;
                aspect-ratio: 1;
                border-radius: .1em;
                background-color: colors.$accent;
                top: 1ch;
                left: -1.5ch;
            }
        }
    }

    .upload
    {
        .steps { width: 50%; }

        .buttons
        {
            position: relative;
            width: 50%;
            justify-content: center;
            align-items: center;
            gap: 5vh;

            input
            {
                font-family: inherit;

                &::file-selector-button
                {
                    font-family: inherit;
                    color: colors.$accent;
                    background: none;
                    margin-left: 20%;
                    margin-right: 1em;
                    padding: .25em 1cqw;
                    border: .15em solid colors.$accent;
                    border-radius: .25em;
                    transition: all ease 250ms;

                    &:hover
                    {
                        cursor: pointer;
                        background-color: colors.$accent;
                        color: white;
                    }
                }
            }

            .loader
            {
                top: 70%;
                height: 50px;
                border-width: .5em;
            }
        }
    }

    .variableContainer
    {
        gap: 3vh;

        &.show { display: flex; }
        &.hide { display: none; }

        .categorical, .numeric, .opentext
        {
            gap: 1vh;

            & > .desc
            {
                position: relative;

                &::after
                {
                    content: '';
                    position: absolute;
                    height: .15em;
                    width: 100%;
                    background-color: colors.$dark80;
                    bottom: 0;
                    left: 0;
                }
            }

            .grid
            {
                grid-template-columns: repeat(5, auto);
                gap: 2em;
        
                .variable
                {
                    align-items: center;
        
                    input[type='checkbox']
                    {
                        position: relative;
                        appearance: none;
                        height: 1.25em;
                        width: 1.25em;
                        border: .15em solid colors.$dark50;
                        border-radius: .2em;
                        margin-right: 1ch;
                        transition: background-color ease 200ms;
        
                        &::after
                        {
                            content: '';
                            position: absolute;
                            height: 100%;
                            width: 100%;
                            border-radius: .1em;
                            scale: .55;
                            background-color: colors.$accent;
                            opacity: 0;
                            transition: opacity ease 200ms;
                        }
        
                        &:hover
                        {
                            cursor: pointer;
                            background-color: colors.$dark50;
                        }
        
                        &:checked::after { opacity: 1; }
                    }
        
                    span
                    {
                        white-space: nowrap;
                        width: 15ch;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        
                    }
                }
            }
        }

        .buttons
        {
            justify-content: center;
            grid-template-columns: repeat(5, auto);
            gap: 2em 0;

            button
            {
                &.show { display: inline-block; }
                &.hide { display: none; }

                font-family: inherit;
                color: colors.$accent;
                height: 100%;
                background: none;
                align-self: center;
                padding: .1em 1cqw;
                margin: 0 1em;
                border: .15em solid colors.$accent;
                border-radius: .25em;
                transition: all ease 250ms;

                &:hover
                {
                    cursor: pointer;
                    background-color: colors.$accent;
                    color: white;
                }
            }
        }

        .placeholder
        {
            .variable
            {
                align-items: center;
    
                input[type='checkbox']
                {
                    position: relative;
                    appearance: none;
                    height: 1.25em;
                    width: 1.25em;
                    border: .15em solid colors.$dark50;
                    border-radius: .2em;
                    margin-right: 1ch;
                    transition: background-color ease 200ms;
    
                    &::after
                    {
                        content: '';
                        position: absolute;
                        height: 100%;
                        width: 100%;
                        border-radius: .1em;
                        scale: .55;
                        background-color: colors.$accent;
                        opacity: 0;
                        transition: opacity ease 200ms;
                    }
    
                    &:hover
                    {
                        cursor: pointer;
                        background-color: colors.$dark50;
                    }
    
                    &:checked::after { opacity: 1; }
                }
            }
        }
    }

    .analysis
    {
        gap: 5vw;

        & > * { width: 50%; }

        &.show { display: flex; }
        &.hide { display: none; }

        .plot, .insights
        {
            position: relative;
            min-height: 30vh;
            gap: 2vh;

            .loader
            {
                align-self: center;
                top: 50%;
                height: 75px;
                border-width: .5em;
            }

            .chart
            {
                min-height: 250px;
                
                .bar, .line { color: colors.$accent; }

                li::before { content: none; }
            }
        }

        .plot
        {
            * { user-select: none; }

            gap: 5vh;

            .wrapper
            {
                display: flex;
                flex-direction: column;
                height: 100%;
                width: 100%;
                gap: 1.5vh;

                align-items: center;
                .descsmall { align-self: center; }

                button
                {
                    font-family: inherit;
                    color: colors.$accent;
                    background: none;
                    width: fit-content;
                    align-self: center;
                    padding: .1em 1cqw;
                    margin-top: 5vh;
                    border: .15em solid colors.$accent;
                    border-radius: .25em;
                    transition: all ease 250ms;
            
                    &:hover
                    {
                        cursor: pointer;
                        background-color: colors.$accent;
                        color: white;
                    }
                }

                .grid
                {
                    align-self: center;
                    
                    gap: .35ch;
                    background-color: colors.$accent;
                    border: .35ch solid colors.$accent;

                    span
                    {
                        background-color: white;
                        text-align: center;
                        align-content: center;
                        padding: .25ch .5ch;

                        &.bold { font-weight: bold; }
                        &.left { text-align: left; }
                    }
                }
            }
        }
    }

    .saveImage
    {
        &.show { display: flex; }
        &.hide { display: none; }
        
        button
        {
            font-family: inherit;
            color: colors.$accent;
            background: none;
            width: fit-content;
            align-self: center;
            padding: .1em 1cqw;
            border: .15em solid colors.$accent;
            border-radius: .25em;
            transition: all ease 250ms;
    
            &:hover
            {
                cursor: pointer;
                background-color: colors.$accent;
                color: white;
            }
        }
    }
}