@use './colors.scss';
@use './global.scss';

#landing
{
    $dark: rgba(0, 0, 0, 0.475);

    .banner
    {
        height: 65vh;
        width: 100%;

        justify-content: center;
        align-items: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../images/landing/banner.webp');
        background-size: cover;
        background-position: center;

        gap: 1.5em;

        .title, .desc
        {
            color: white;
            margin: 0 25%;
        }
    }

    .big, .small { margin: 5vh 10vw; }

    .big
    {
        height: calc(85vh - global.$navHeight);
        justify-content: center;
        gap: 12.5vw;
        align-items: center;
        
        img
        {
            height: auto;
            width: 40%;
            border-radius: 1em;
        }

        .content
        {
            gap: 3.5em;
            max-width: 500px;

            button
            {
                font-family: inherit;
                font-size: 1.5em;
                color: colors.$accent;
                background: none;
                height: 5ch;
                width: fit-content;
                margin: 0;
                padding: 0 1.5cqw;
                border: .2em solid colors.$accent;
                border-radius: .25em;
                transition: all ease 250ms;

                &:hover
                {
                    cursor: pointer;
                    background-color: colors.$accent;
                    color: white;
                }
            }
        }
    }

    .small
    {
        .extra { margin-bottom: .75em; }

        .title
        {
            position: relative;

            &::after
            {
                content: '';
                position: absolute;
                bottom: -.5ch;
                left: 0;
                height: .2ch;
                width: 100%;
                background-color: black;
                border-radius: 1em;
            }
        }

        svg
        {
            color: colors.$accent;
            height: 2.5rem;
            width: 2.5rem;
        }

        .table1
        {
            padding: 5vh 0;
            gap: 2.5ch 5cqw;
            grid-template: repeat(3, 1fr) / repeat(3, 1fr);

            & > *
            {
                gap: 1cqw;
                align-items: center;
            }
        }

        .table2
        {
            .desc { font-weight: bold; }

            padding: 5vh 0;
            gap: 2.5ch 3.5cqw;
            grid-template: repeat(2, 1fr) / repeat(3, 1fr);
            
            & > * { gap: 1.5ch; }

            div:has(> .desc)
            {
                align-items: center;
                gap: 1em;
            }
        }

        .table3
        {
            padding: 5vh 0;
            gap: 2.5ch 3.75cqw;
            grid-template: repeat(2, 1fr) / repeat(3, 1fr);
            
            & > *
            {
                gap: 1cqw;
                align-items: center;
            }
        }
    }
}