@use './colors.scss';

.usecase
{
    $dark: rgba(0, 0, 0, 0.475);
    gap: 5vh;

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../images/usecase/banner.webp');
        background-size: cover;
        background-position: center;

        .title, .desc
        {
            color: white;
            margin: 0 10vw;
        }
    }

    .section
    {
        justify-content: center;
        margin: 0 10vw;
        gap: 1.5ch;

        ul
        {
            margin: 0;
            list-style: none;
            
            li
            {
                position: relative;

                &::before
                {
                    position: absolute;
                    content: '';
                    height: .5em;
                    aspect-ratio: 1;
                    border-radius: .1em;
                    background-color: colors.$accent;
                    top: 1ch;
                    left: -1.5ch;
                }
            }
        }

        .notTitle
        {
            gap: 1.5ch;
            margin-left: 5ch;

            .part
            {
                & > .desc { font-weight: bold; }
            }
        }
    }

    .grid
    {
        align-self: center;
        margin: 2vh 0;
        
        gap: .35ch;
        background-color: colors.$accent;
        border: .35ch solid colors.$accent;

        span 
        {
            background-color: white;
            text-align: center;
            align-content: center;
            padding: .25ch 2ch;

            &.bold { font-weight: bold; }
            &.left { text-align: left; }
            &.right { text-align: right; }
        }

        &._5x4
        {
            grid-template-columns: repeat(5, auto);
            grid-template-rows: repeat(4, auto);
        }

        &._2x4
        {
            grid-template-columns: repeat(2, auto);
            grid-template-rows: repeat(4, auto);
        }
        
        &._4x4
        {
            grid-template-columns: repeat(4, auto);
            grid-template-rows: repeat(4, auto);
        }

        &._3x3
        {
            grid-template-columns: repeat(3, auto);
            grid-template-rows: repeat(3, auto);
        }

        &._2x3
        {
            grid-template-columns: repeat(2, auto);
            grid-template-rows: repeat(3, auto);
        }

        &._4x5
        {
            grid-template-columns: repeat(4, auto);
            grid-template-rows: repeat(5, auto);
        }
    }
}