@use './colors.scss';

#test
{   
    .wrapper
    {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        gap: 1.5vh;

        align-items: center;
        .descsmall { align-self: center; }

        button
        {
            font-family: inherit;
            color: colors.$accent;
            background: none;
            width: fit-content;
            align-self: center;
            padding: .1em 1cqw;
            margin-top: 5vh;
            border: .15em solid colors.$accent;
            border-radius: .25em;
            transition: all ease 250ms;
    
            &:hover
            {
                cursor: pointer;
                background-color: colors.$accent;
                color: white;
            }
        }

        .grid
        {
            align-self: center;
            
            gap: .35ch;
            background-color: colors.$accent;
            border: .35ch solid colors.$accent;

            span
            {
                background-color: white;
                text-align: center;
                align-content: center;
                padding: .25ch .5ch;

                &.bold { font-weight: bold; }
                &.left { text-align: left; }
            }
        }
    }
}