@use './colors.scss';

@import url('https://fonts.googleapis.com/css2?family=Tinos:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=REM:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

$navHeight: 12.5vh;

:root { font-size: 10px; }

*
{
    user-select: text;

    &::-webkit-scrollbar
    {
        background-color: colors.$dark30;
        height: 10px;
        width: 10px;
    }

    &::-webkit-scrollbar-thumb
    {
        background-color: colors.$dark70;
        border-radius: 5em;

        &:hover { cursor: pointer; }
    }

    &::selection
    {
        background-color: colors.$accent;
        color: white;
    }
}

.row
{
    display: flex;
    flex-direction: row;
}

.column
{
    display: flex;
    flex-direction: column;
}

.grid { display: grid; }

.bold { font-weight: bold; }

body
{
    height: 100vh;
    width: 100%;

    display: flex;
    flex-direction: column;

    // font-family: 'Ubuntu';
    // font-family: 'Tinos';
    font-family: 'REM';

    margin: 0;
    padding: 0;

    background-color: white;
}

#nav
{
    position: fixed;
    z-index: 100;

    height: $navHeight;

    display: grid;
    grid-template-columns: repeat(7, 1fr);
    align-items: center;
    text-align: center;
    justify-items: center;

    font-size: 1.5em;

    margin: 0;
    padding: 0 2vw;
    
    background-color: hsla(0, 0%, 100%, 0.95);
    box-shadow: 0 -20px 40px 0px colors.$dark50;

    backdrop-filter: blur(10px);

    img
    {
        height: auto;
        width: 50%;

        &:hover { cursor: pointer; }
    }

    .navItem
    {
        position: relative;
        height: 100%;
        align-content: center;
        transition: color ease 200ms;

        &::after
        {
            content: '';
            position: absolute;
            bottom: 2.5ch;
            left: 0;
            width: 100%;
            height: 0;
            background-color: colors.$accent;
            transition: height ease 200ms;
        }

        &:hover
        {
            cursor: pointer;
            color: colors.$accent;

            &::after { height: .25ch; }
        }
    }

    .contact
    {
        font-family: inherit;
        background: none;
        color: white;
        width: fit-content;
        margin: 0;
        padding: .5ch 1cqw;
        background-color: colors.$accent;
        border: .2em solid colors.$accent;
        border-radius: .25em;
        transition: all ease 250ms;

        &:hover
        {
            cursor: pointer;
            color: colors.$accent;
            background-color: white;
        }
    }

    *[data-link="true"]
    {
        position: relative;
        width: fit-content;

        &::before
        {
            position: absolute;
            content: url('../svg/ChevronRightPink1.svg');
            right: -1.75ch;
            height: 1rem;
            aspect-ratio: 1;
            opacity: 0;
            transition: opacity ease 250ms;
        }

        &:hover::before { opacity: 1; }
    }

    .float
    {
        position: absolute;
        top: $navHeight - 1vh;
        background-color: white;
        border-radius: .25em;
        box-shadow: 0 0 10px 0 colors.$dark50;

        .container
        {
            display: none;
            padding: 2ch 2cqw;
            text-align: left;
            gap: 1em;

            span
            {
                transition: color ease 250ms;

                &:hover
                {
                    cursor: pointer;
                    color: colors.$accent;
                }
            }
        }
    }
}

#main
{
    flex-grow: 1;
    margin-top: $navHeight;

    .title
    {
        font-size: 3.5rem;
        line-height: 2ch;
        font-weight: bold;
    }

    .desc
    {
        font-size: 2.1rem;
        line-height: 2.5ch;
    }

    .descsmall
    {
        font-size: 1.8rem;
        line-height: 2.75ch;
    }
}

#footer
{
    font-size: 1.25em;
    padding: 1.25ch 0;
    margin-top: 5vh;
    background-color: colors.$dark25;
    color: white;
    justify-content: center;
}