#MII
{
    $dark: rgba(0, 0, 0, 0.475);

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../../images/sample/MII/banner.webp');
        background-size: cover;
        background-position: center;

        .title, .desc { color: white; }
    }

    .section
    {
        justify-content: center;
        margin: 5vh 10vw;
        gap: 2em;

        img
        {
            height: auto;
            align-self: center;
        }

        &[data-id='1']
        {
            img[alt='firstImage'] { width: 35%; }
            img[alt='secondImage'] { width: 55%; }

            .images
            {
                gap: 5vw;
                justify-content: center;
            }

            img[alt='thirdImage'] { width: 40%; }

            & > div:last-of-type
            {
                gap: 10em;

                .column { gap: 1.5em; }
            }
        }

        &[data-id='2']
        {
            img { width: 30%; }

            & > div:first-of-type .desc { margin-bottom: 1ch; }

            .row:has(img)
            {
                gap: 5em;
                justify-content: center;
            }
        }

        &[data-id='3']
        {
            img { width: 90%; }
        }
    }
}