@use '../colors.scss';

#CX
{
    $dark: rgba(0, 0, 0, 0.475);

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../../images/sample/CX/banner.webp');
        background-size: cover;
        background-position: center;

        .title
        {
            color: white;
            margin: 0 37%;
        }
    }

    .section
    {
        justify-content: center;
        margin: 5vh 10vw;

        img
        {
            height: auto;
            align-self: center;
            margin: 2.5em 0;
        }

        .container
        {
            align-items: center;
            margin: 1em 0;

            & > .column { gap: 1em; }
        }

        &[data-id='1']
        {
            .container
            {
                gap: 3em;

                img { width: 100%; }

                .grid
                {
                    margin-top: 2.5vh;
                    grid-template-rows: repeat(4, auto);
                    grid-template-columns: repeat(6, auto);
    
                    gap: .1ch;
                    background-color: colors.$accentBlue4;
                    border: .1ch solid colors.$accentBlue4;

                    span 
                    {
                        background-color: white;
                        text-align: center;
                        align-content: center;
                        padding: 0 .2em;

                        &.bold { font-weight: bold; }
                    }
                }
            }
        }

        &[data-id='2']
        {
            img { width: 50%; }

            .container { gap: 5em; }
        }

        &[data-id='3']
        {
            img { width: 55%; }
            
            .container
            {
                gap: 5em;

                & > .column { gap: 2em; }

                .grid
                {
                    grid-template-rows: repeat(4, auto);
                    grid-template-columns: repeat(2, auto);
    
                    gap: .25ch;
                    background-color: colors.$accentOrange2;
                    border: .25ch solid colors.$accentOrange2;

                    span
                    {
                        background-color: white;
                        text-align: center;
                        align-content: center;
                        padding: 0 .5em;

                        &.bold { font-weight: bold; }
                        &.left { text-align: left; }
                    }
                }
            }
        }
    }
}