@use './colors.scss';

#contact
{
    margin: 0 10vw;
    padding: 5vh 0;
    height: 100%;

    align-items: center;
    gap: 7vw;

    .left
    {
        #emailid
        {
            position: relative;
            display: flex;
            flex-direction: row;
            gap: 2cqw;
            width: fit-content;
            color: white;
            font-family: inherit;
            margin: 5vh 0;
            padding: .5ch 1ch;
            background-color: colors.$accent;
            border: .3ch solid colors.$accent;
            border-radius: .75ch;
            box-shadow: 0 0 1em -.25em colors.$dark60;
            transition: all ease 200ms;

            * { pointer-events: none; }

            .email { transition: all ease 200ms; }

            &::after
            {
                content: url('../svg/ChevronRightPink1.svg');
                position: absolute;
                rotate: 90deg;
                bottom: -1.5ch;
                left: 50%;
                height: 10px;
                aspect-ratio: 1;
                opacity: 0;
                transition: opacity ease 200ms;
            }

            &:hover
            {
                cursor: pointer;
                color: black;
                background-color: white;

                .email { color: colors.$accent; }

                .alert { opacity: 1; }

                &::after { opacity: 1; }
            }

            .mailIcon
            {
                justify-content: center;
                margin-left: 1cqw;

                svg
                {
                    $HW: 15/18.75;
                    color: colors.$accent;
                    scale: 2;
                    background-color: white;
                    padding: .25ch*$HW .25ch;
                    border-radius: .15em;
                }
            }

            div:has(.email) { text-align: left; }

            .alert
            {
                position: absolute;
                left: 0;
                right: 0;
                bottom: -3.5ch;
                align-items: center;
                justify-content: center;
                gap: .25cqw;
                opacity: 0;
                transition: opacity ease 200ms;
                
                svg { color: colors.$accent; }
            }
        }
    }

    .right
    {
        flex-grow: 1;
        background-color: colors.$accent;
        padding: 2ch 4ch;
        color: white;
        border-radius: .75em;
        gap: 2em;
        
        span { line-height: normal; }
        
        form
        {
            display: flex;
            flex-direction: column;
            gap: 2em;
            margin-bottom: calc(2em - 2ch);
            
            *
            {
                font-family: inherit;
                font-size: 1.35em;
                border: .15em solid transparent;
                background-color: white;
                color: black;
                padding: 1ch 2ch;
                border-radius: .25em;
                caret-color: colors.$accent;
                transition: all ease 200ms;

                &:focus { outline: none; }

                &[name='message']
                {
                    resize: vertical;
                    height: 10vh;

                    &::-webkit-scrollbar
                    {
                        background-color: transparent;
                        width: .5ch;
                    }

                    &::-webkit-scrollbar-thumb
                    {
                        background-color: colors.$accent;
                        border-radius: 5em;

                        &:hover { cursor: pointer; }
                    }
                }

                &[type='submit']:hover
                {
                    cursor: pointer;
                    font-size: 1.65em;
                    color: white;
                    background-color: transparent;
                    border-color: white;
                }
            }
        }
    }
}