@use './colors.scss';

#perspective
{
    $dark: rgba(0, 0, 0, 0.475);

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../images/perspective/banner.webp');
        background-size: cover;
        background-position: center;

        .title, .desc { color: white; }
    }

    .grid
    {
        margin: 5vh 10vw;
        grid-template: repeat(3, 1fr) / repeat(3, 1fr);
        gap: 7vh 0;
        align-items: start;
        justify-items: center;

        & > .column
        {
            position: relative;
            width: 75%;
            align-items: center;
            justify-content: center;
            gap: 2vh;

            * { pointer-events: none; }

            img
            {
                height: auto;
                width: 100%;
                align-self: center;
                border-radius: 1em;
                transition: box-shadow 200ms ease;
            }

            span { text-align: center; }

            &::after
            {
                position: absolute;
                content: '';
                height: .4em;
                width: 100%;
                top: 42.5vh;
                background-color: colors.$accent;
                opacity: 0;
                border-radius: 1em;
                transition: opacity 200ms ease;
            }

            &:hover
            {
                cursor: pointer;

                img { box-shadow: 0 1em 1.5em -1.5em colors.$accent; }

                &::after { opacity: 1; }
            }
        }
    }
}

.perspectives
{
    $dark: rgba(0, 0, 0, 0.475);
    gap: 5vh;

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../images/perspective/perspectivesBanner.webp');
        background-size: cover;
        background-position: center;

        .title, .desc
        {
            color: white;
            margin: 0 10vw;
        }
    }

    .section
    {
        justify-content: center;
        margin: 0 10vw;
        gap: 1.5ch;

        ul
        {
            margin: 0;
            list-style: none;
            
            li
            {
                position: relative;

                &::before
                {
                    position: absolute;
                    content: '';
                    height: .5em;
                    aspect-ratio: 1;
                    border-radius: .1em;
                    background-color: colors.$accent;
                    top: 1ch;
                    left: -1.5ch;
                }
            }
        }
    }
}