@use '../colors.scss';

#EJI
{
    $dark: rgba(0, 0, 0, 0.475);

    .banner
    {
        height: 50vh;
        width: 100%;

        justify-content: center;
        text-align: center;

        background: linear-gradient($dark, $dark), url('../../images/sample/EJI/banner.webp');
        background-size: cover;
        background-position: center;

        .title
        {
            color: white;
            margin: 0 37%;
        }
    }

    .section
    {
        justify-content: center;
        margin: 5vh 10vw;

        img
        {
            height: auto;
            align-self: center;
            margin: 2.5em 0;
        }

        .container
        {
            align-items: center;
            margin: 1em 0;

            & > .column { gap: 1em; }                
        }


        &[data-id='1']
        {
            img { width: 80%; }

            .descsmall { margin: .5ch 0; }
        }

        &[data-id='2']
        {
            .container
            {
                gap: 5em;

                img { width: 50%; }

                .grid
                {
                    height: fit-content;
                    margin-top: 2.5vh;
                    grid-template-columns: .7fr .45fr .7fr;
                    grid-template-rows: fit-content(1ch) repeat(3, fit-content(3ch));
                    // grid-template-rows: repeat(4, auto);
                    // grid-template-columns: repeat(3, auto);
    
                    gap: .1ch;
                    background-color: colors.$accentOrange1;
                    border: .1ch solid colors.$accentOrange1;

                    span 
                    {
                        background-color: white;
                        text-align: center;
                        align-content: center;

                        &.bold { font-weight: bold; }
                    }
                }
            }
        }

        &[data-id='3']
        {
            img { width: 55%; }
        }

        &[data-id='4']
        {
            img { width: 40%; }
            
            .container { gap: 5em; }
        }

        &[data-id='5']
        {
            img { width: 60%; }
            
            .container { gap: 2em; }

            .grid
            {
                grid-template-rows: repeat(4, auto);
                grid-template-columns: repeat(5, auto);

                gap: .1ch;
                background-color: colors.$accentBlue4;
                // border: .1ch solid colors.$accentBlue4;

                span 
                {
                    background-color: white;
                    text-align: center;
                    align-content: center;

                    &.bold { font-weight: bold; }

                    &.left
                    {
                        text-align: left;
                        padding: 0 1em;
                    }
                }
            }
        }
    }
}